import React, { useEffect, useState } from 'react';
import {
  Button,
  Form,
  Label,
  Popup,
  Pagination,
  Table,
} from 'semantic-ui-react';
import { Link } from 'react-router-dom';

import {
  API,
  copy,
  showError,
  showInfo,
  showSuccess,
  showWarning,
  timestamp2string,
} from '../helpers';
import { renderQuota } from '../helpers/render';

import { ITEMS_PER_PAGE } from '../constants';

/**
 * 
 * @param {number} timestamp 
 * @returns {string} 
 */
function renderTimestamp(timestamp) {
  return <>{timestamp2string(timestamp)}</>;
}
/**
 * 
 * @param {number} status 
 * @returns {ReactNode} 
 */
function renderStatus(status) {
  switch (status) {
    case 1:
      return (
        <Label basic color='green'>
          
        </Label>
      );
    case 2:
      return (
        <Label basic color='red'>
          
        </Label>
      );
    case 3:
      return (
        <Label basic color='grey'>
          
        </Label>
      );
    default:
      return (
        <Label basic color='black'>
          
        </Label>
      );
  }
}

const RedemptionsTable = () => {
  const [loading, setLoading] = useState(true); // 
  const [searching, setSearching] = useState(false); // 
  const [activePage, setActivePage] = useState(1); // 
  const [redemptions, setRedemptions] = useState([]); // 
  const [searchKeyword, setSearchKeyword] = useState(''); // 

  /**
   * 
   * @param {number} startIdx - 
   * @returns {void}
   */
  const loadRedemptions = async (startIdx) => {
    try {
      // API
      const res = await API.get(`/api/redemption/?p=${startIdx}`);
      const { success, message, data } = res.data;

      if (success) {
        // 
        if (startIdx === 0) {
          // redemptions
          setRedemptions(data);
        } else {
          // 
          let newRedemptions = [...redemptions];
          newRedemptions.push(...data);
          setRedemptions(newRedemptions);
        }
      } else {
        // 
        showError(message);
      }

      // loadingfalse
      setLoading(false);
    } catch (error) {
      // 
      console.error('Error while loading redemptions:', error);
      showError('Failed to load redemptions');
      setLoading(false);
    }
  };

  /**
   * 
   * @param {Event} e - 
   * @param {Object} data - activePage
   * @returns {void}
   */
  const onPaginationChange = (e, { activePage }) => {
    (async () => {
      if (activePage === Math.ceil(redemptions.length / ITEMS_PER_PAGE) + 1) {
        // 
        await loadRedemptions(activePage - 1);
      }
      // 
      setActivePage(activePage);
    })();
  };

  /**
   * 
   * @returns {void}
   */
  useEffect(() => {
    loadRedemptions(0)
      .then()
      .catch((reason) => {
        showError(reason);
      });
  }, []);

  /**
   * 
   * @param {number} id - ID
   * @param {string} action -  ('delete', 'enable', 'disable')
   * @param {number} idx - 
   * @returns {void}
   */
  const manageRedemption = async (id, action, idx) => {
    let data = { id };
    let res;
    switch (action) {
      case 'delete':
        res = await API.delete(`/api/redemption/${id}/`);
        break;
      case 'enable':
        data.status = 1;
        res = await API.put('/api/redemption/?withStatus=true', data);
        break;
      case 'disable':
        data.status = 2;
        res = await API.put('/api/redemption/?withStatus=true', data);
        break;
    }
    const { success, message } = res.data;
    if (success) {
      showSuccess('');
      let redemption = res.data.data;
      let newRedemptions = [...redemptions];
      let realIdx = (activePage - 1) * ITEMS_PER_PAGE + idx;
      if (action === 'delete') {
        newRedemptions[realIdx].deleted = true;
      } else {
        newRedemptions[realIdx].status = redemption.status;
      }
      setRedemptions(newRedemptions);
    } else {
      showError(message);
    }
  };
  /**
   * 
   * 
   * @returns {void}
   */
  const searchRedemptions = async () => {
    if (searchKeyword === '') {
      // 
      await loadRedemptions(0);
      setActivePage(1);
      return;
    }
    setSearching(true);
    const res = await API.get(
      `/api/redemption/search?keyword=${searchKeyword}`
    );
    const { success, message, data } = res.data;
    if (success) {
      setRedemptions(data);
      setActivePage(1);
    } else {
      showError(message);
    }
    setSearching(false);
  };

  /**
   * 
   * @param {Object} e - 
   * @param {Object} value - 
   * @returns {void}
   */
  const handleKeywordChange = async (e, { value }) => {
    setSearchKeyword(value.trim());
  };

  /**
   * 
   * @param {string} key - 
   * @returns {void}
   */
  const sortRedemption = (key) => {
    if (redemptions.length === 0) return;
    setLoading(true);
    let sortedRedemptions = [...redemptions];
    sortedRedemptions.sort((a, b) => {
      if (!isNaN(a[key])) {
        // 
        return a[key] - b[key];
      } else {
        // 
        return ('' + a[key]).localeCompare(b[key]);
      }
    });
    if (sortedRedemptions[0].id === redemptions[0].id) {
      sortedRedemptions.reverse();
    }
    setRedemptions(sortedRedemptions);
    setLoading(false);
  };

  return (
    <>
      <Form onSubmit={searchRedemptions}>
        <Form.Input
          icon='search'
          fluid
          iconPosition='left'
          placeholder=' ID  ...'
          value={searchKeyword}
          loading={searching}
          onChange={handleKeywordChange}
        />
      </Form>

      <Table basic compact size='small'>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortRedemption('id');
              }}
            >
              ID
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortRedemption('name');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortRedemption('status');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortRedemption('quota');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortRedemption('createTime');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortRedemption('redeemedTime');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell></Table.HeaderCell>
          </Table.Row>
        </Table.Header>

        <Table.Body>
          {redemptions
            .slice(
              (activePage - 1) * ITEMS_PER_PAGE,
              activePage * ITEMS_PER_PAGE
            )
            .map((redemption, idx) => {
              if (redemption.deleted) return <></>;
              return (
                <Table.Row key={redemption.id}>
                  <Table.Cell>{redemption.id}</Table.Cell>
                  <Table.Cell>
                    {redemption.name ? redemption.name : ''}
                  </Table.Cell>
                  <Table.Cell>{renderStatus(redemption.status)}</Table.Cell>
                  <Table.Cell>{renderQuota(redemption.quota)}</Table.Cell>
                  <Table.Cell>
                    {renderTimestamp(redemption.createTime)}
                  </Table.Cell>
                  <Table.Cell>
                    {redemption.redeemedTime
                      ? renderTimestamp(redemption.redeemedTime)
                      : ''}{' '}
                  </Table.Cell>
                  <Table.Cell>
                    <div>
                      <Button
                        size={'small'}
                        positive
                        onClick={async () => {
                          if (await copy(redemption.key)) {
                            showSuccess('');
                          } else {
                            showWarning(
                              ''
                            );
                            setSearchKeyword(redemption.key);
                          }
                        }}
                      >
                        
                      </Button>
                      <Popup
                        trigger={
                          <Button size='small' negative>
                            
                          </Button>
                        }
                        on='click'
                        flowing
                        hoverable
                      >
                        <Button
                          negative
                          onClick={() => {
                            manageRedemption(redemption.id, 'delete', idx);
                          }}
                        >
                          
                        </Button>
                      </Popup>
                      <Button
                        size={'small'}
                        disabled={redemption.status === 3} // used
                        onClick={() => {
                          manageRedemption(
                            redemption.id,
                            redemption.status === 1 ? 'disable' : 'enable',
                            idx
                          );
                        }}
                      >
                        {redemption.status === 1 ? '' : ''}
                      </Button>
                      <Button
                        size={'small'}
                        as={Link}
                        to={'/redemption/edit/' + redemption.id}
                      >
                        
                      </Button>
                    </div>
                  </Table.Cell>
                </Table.Row>
              );
            })}
        </Table.Body>

        <Table.Footer>
          <Table.Row>
            <Table.HeaderCell colSpan='8'>
              <Button
                size='small'
                as={Link}
                to='/redemption/add'
                loading={loading}
              >
                
              </Button>
              <Pagination
                floated='right'
                activePage={activePage}
                onPageChange={onPaginationChange}
                size='small'
                siblingRange={1}
                totalPages={
                  Math.ceil(redemptions.length / ITEMS_PER_PAGE) +
                  (redemptions.length % ITEMS_PER_PAGE === 0 ? 1 : 0)
                }
              />
            </Table.HeaderCell>
          </Table.Row>
        </Table.Footer>
      </Table>
    </>
  );
};

export default RedemptionsTable;
