import React, { useContext, useEffect, useState } from 'react';
import {
  Button,
  Divider,
  Form,
  Header,
  Image,
  Message,
  Modal,
} from 'semantic-ui-react';
import { Link, useNavigate } from 'react-router-dom';
import Turnstile from 'react-turnstile';

import {
  API,
  copy,
  showError,
  showInfo,
  showNotice,
  showSuccess,
} from '../helpers';
import { onGitHubOAuthClicked } from './utils';
import { UserContext } from '../context/User';

const PersonalSetting = () => {
  let navigate = useNavigate(); // 

  const [loading, setLoading] = useState(false); // 
  const [countdown, setCountdown] = useState(30); // 
  const [disableButton, setDisableButton] = useState(false); // 
  const [turnstileEnabled, setTurnstileEnabled] = useState(false); // Turnstile 
  const [turnstileToken, setTurnstileToken] = useState(''); // Turnstile Token 
  const [turnstileSiteKey, setTurnstileSiteKey] = useState(''); // Turnstile Site Key 
  const [showWeChatBindModal, setShowWeChatBindModal] = useState(false); // 
  const [showEmailBindModal, setShowEmailBindModal] = useState(false); // 
  const [showAccountDeleteModal, setShowAccountDeleteModal] = useState(false); // 

  const [status, setStatus] = useState({}); // 
  const [affLink, setAffLink] = useState(''); // 
  const [systemToken, setSystemToken] = useState(''); //  Token 

  const [userState, userDispatch] = useContext(UserContext); // 
  const [inputs, setInputs] = useState({
    email: '', // 
    emailVerificationCode: '', // 
    wechatVverificationCode: '', // 
    selfAaccountDeletionConfirmation: '', // 
  });
  // 

  useEffect(() => {
    // 
    let status = localStorage.getItem('status');
    if (status) {
      // 
      status = JSON.parse(status);
      setStatus(status); // 
      if (status.turnstileCheckEnabled) {
        setTurnstileEnabled(true); //  Turnstile
        setTurnstileSiteKey(status.turnstileSiteKey); //  Turnstile Site Key
      }
    }
  }, []);

  useEffect(() => {
    let countdownInterval = null;
    if (disableButton && countdown > 0) {
      // 0
      countdownInterval = setInterval(() => {
        setCountdown(countdown - 1); // 
      }, 1000);
    } else if (countdown === 0) {
      // 0
      setDisableButton(false); // 
      setCountdown(30); // 
    }
    return () => clearInterval(countdownInterval); // 
  }, [disableButton, countdown]);

  /**
   * 
   * @param {Object} e - 
   * @param {Object} param1 -  name  value 
   */
  const handleInputChange = (e, { name, value }) => {
    setInputs((inputs) => ({ ...inputs, [name]: value }));
  };

  /**
   * 
   */
  const generateAccessToken = async () => {
    const res = await API.get('/api/user/token');
    const { success, message, data } = res.data;

    if (success) {
      // 
      setSystemToken(data); // 
      setAffLink(''); // 
      await copy(data); // 
      showSuccess(``); // 
    } else {
      // 
      showError(message); // 
    }
  };

  /**
   * 
   */
  const getAffLink = async () => {
    const res = await API.get('/api/user/aff');
    const { success, message, data } = res.data;

    if (success) {
      // 
      let link = `${window.location.origin}/register?aff=${data}`; // 
      setAffLink(link); // 
      setSystemToken(''); // 
      await copy(link); // 
      showSuccess(``); // 
    } else {
      // 
      showError(message); // 
    }
  };
  /**
   * 
   * @param {Event} e 
   */
  const handleAffLinkClick = async (e) => {
    e.target.select(); // 
    await copy(e.target.value); // 
    showSuccess(``); // 
  };

  /**
   * 
   * @param {Event} e 
   */
  const handleSystemTokenClick = async (e) => {
    e.target.select(); // 
    await copy(e.target.value); // 
    showSuccess(``); // 
  };

  /**
   * 
   */
  const deleteAccount = async () => {
    if (inputs.selfAaccountDeletionConfirmation !== userState.user.userName) {
      showError(''); // 
      return;
    }

    const res = await API.delete('/api/user/self');
    const { success, message } = res.data;

    if (success) {
      showSuccess(''); // 
      await API.get('/api/user/logout');
      userDispatch({ type: 'logout' });
      localStorage.removeItem('user');
      navigate('/login');
    } else {
      showError(message); // 
    }
  };

  /**
   * 
   */
  const bindWeChat = async () => {
    if (inputs.wechatVverificationCode === '') return;
    const res = await API.get(
      `/api/oauth/wechat/bind?code=${inputs.wechatVverificationCode}`
    );
    const { success, message } = res.data;
    if (success) {
      showSuccess(''); // 
      setShowWeChatBindModal(false);
    } else {
      showError(message); // 
    }
  };

  /**
   * 
   */
  const sendVerificationCode = async () => {
    setDisableButton(true);
    if (inputs.email === '') return;
    if (turnstileEnabled && turnstileToken === '') {
      showInfo('Turnstile '); // 
      return;
    }
    setLoading(true);
    const res = await API.get(
      `/api/verification?email=${inputs.email}&turnstile=${turnstileToken}`
    );
    const { success, message } = res.data;
    if (success) {
      showSuccess(''); // 
    } else {
      showError(message); // 
    }
    setLoading(false);
  };

  /**
   * 
   */
  const bindEmail = async () => {
    if (inputs.emailVerificationCode === '') return;
    setLoading(true);
    const res = await API.get(
      `/api/oauth/email/bind?email=${inputs.email}&code=${inputs.emailVerificationCode}`
    );
    const { success, message } = res.data;
    if (success) {
      showSuccess(''); // 
      setShowEmailBindModal(false);
    } else {
      showError(message); // 
    }
    setLoading(false);
  };

  return (
    <div style={{ lineHeight: '40px' }}>
      <Header as='h3'></Header>
      <Message>
         OpenAI
        
      </Message>
      <Button as={Link} to={`/user/edit/`}>
        
      </Button>
      <Button onClick={generateAccessToken}></Button>
      <Button onClick={getAffLink}></Button>
      <Button
        onClick={() => {
          setShowAccountDeleteModal(true);
        }}
      >
        
      </Button>

      {systemToken && (
        <Form.Input
          fluid
          readOnly
          value={systemToken}
          onClick={handleSystemTokenClick}
          style={{ marginTop: '10px' }}
        />
      )}
      {affLink && (
        <Form.Input
          fluid
          readOnly
          value={affLink}
          onClick={handleAffLinkClick}
          style={{ marginTop: '10px' }}
        />
      )}
      <Divider />
      <Header as='h3'></Header>
      {status.weChatAuthEnabled && (
        <Button
          onClick={() => {
            setShowWeChatBindModal(true);
          }}
        >
          
        </Button>
      )}
      <Modal
        onClose={() => setShowWeChatBindModal(false)}
        onOpen={() => setShowWeChatBindModal(true)}
        open={showWeChatBindModal}
        size={'mini'}
      >
        <Modal.Content>
          <Modal.Description>
            <Image src={status.wechatQRcode} fluid />
            <div style={{ textAlign: 'center' }}>
              <p>
                
              </p>
            </div>
            <Form size='large'>
              <Form.Input
                fluid
                placeholder=''
                name='wechatVverificationCode'
                value={inputs.wechatVverificationCode}
                onChange={handleInputChange}
              />
              <Button color='' fluid size='large' onClick={bindWeChat}>
                
              </Button>
            </Form>
          </Modal.Description>
        </Modal.Content>
      </Modal>
      {status.gitHubOAuthEnabled && (
        <Button
          onClick={() => {
            onGitHubOAuthClicked(status.gitHubClientId);
          }}
        >
           GitHub 
        </Button>
      )}
      <Button
        onClick={() => {
          setShowEmailBindModal(true);
        }}
      >
        
      </Button>
      <Modal
        onClose={() => setShowEmailBindModal(false)}
        onOpen={() => setShowEmailBindModal(true)}
        open={showEmailBindModal}
        size={'tiny'}
        style={{ maxWidth: '450px' }}
      >
        <Modal.Header></Modal.Header>
        <Modal.Content>
          <Modal.Description>
            <Form size='large'>
              <Form.Input
                fluid
                placeholder=''
                onChange={handleInputChange}
                name='email'
                type='email'
                action={
                  <Button
                    onClick={sendVerificationCode}
                    disabled={disableButton || loading}
                  >
                    {disableButton ? `(${countdown})` : ''}
                  </Button>
                }
              />
              <Form.Input
                fluid
                placeholder=''
                name='emailVerificationCode'
                value={inputs.emailVerificationCode}
                onChange={handleInputChange}
              />
              {turnstileEnabled ? (
                <Turnstile
                  sitekey={turnstileSiteKey}
                  onVerify={(token) => {
                    setTurnstileToken(token);
                  }}
                />
              ) : (
                <></>
              )}
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  marginTop: '1rem',
                }}
              >
                <Button
                  color=''
                  fluid
                  size='large'
                  onClick={bindEmail}
                  loading={loading}
                >
                  
                </Button>
                <div style={{ width: '1rem' }}></div>
                <Button
                  fluid
                  size='large'
                  onClick={() => setShowEmailBindModal(false)}
                >
                  
                </Button>
              </div>
            </Form>
          </Modal.Description>
        </Modal.Content>
      </Modal>
      <Modal
        onClose={() => setShowAccountDeleteModal(false)}
        onOpen={() => setShowAccountDeleteModal(true)}
        open={showAccountDeleteModal}
        size={'tiny'}
        style={{ maxWidth: '450px' }}
      >
        <Modal.Header></Modal.Header>
        <Modal.Content>
          <Message></Message>
          <Modal.Description>
            <Form size='large'>
              <Form.Input
                fluid
                placeholder={` ${userState?.user?.userName} `}
                name='selfAaccountDeletionConfirmation'
                value={inputs.selfAaccountDeletionConfirmation}
                onChange={handleInputChange}
              />
              {turnstileEnabled ? (
                <Turnstile
                  sitekey={turnstileSiteKey}
                  onVerify={(token) => {
                    setTurnstileToken(token);
                  }}
                />
              ) : (
                <></>
              )}
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  marginTop: '1rem',
                }}
              >
                <Button
                  color='red'
                  fluid
                  size='large'
                  onClick={deleteAccount}
                  loading={loading}
                >
                  
                </Button>
                <div style={{ width: '1rem' }}></div>
                <Button
                  fluid
                  size='large'
                  onClick={() => setShowAccountDeleteModal(false)}
                >
                  
                </Button>
              </div>
            </Form>
          </Modal.Description>
        </Modal.Content>
      </Modal>
    </div>
  );
};

export default PersonalSetting;
