import React, { useEffect, useState } from 'react';
import {
  Button,
  Form,
  Grid,
  Header,
  Image,
  Message,
  Segment,
} from 'semantic-ui-react';
import { Link, useNavigate } from 'react-router-dom';
import Turnstile from 'react-turnstile';

import { API, getLogo, showError, showInfo, showSuccess } from '../helpers';

const RegisterForm = () => {
  let navigate = useNavigate();

  // 
  const [loading, setLoading] = useState(false);
  // turnstileEnabled
  const [turnstileEnabled, setTurnstileEnabled] = useState(false);
  // turnstileToken
  const [turnstileToken, setTurnstileToken] = useState('');
  // turnstileSiteKey
  const [turnstileSiteKey, setTurnstileSiteKey] = useState('');
  // showEmailVerification
  const [showEmailVerification, setShowEmailVerification] = useState(false);
  // useStateinputsstatestatesetInputs
  const [inputs, setInputs] = useState({
    // 
    userName: '',
    // 
    password: '',
    // 
    password2: '',
    // 
    email: '',
    // 
    verificationCode: '',
  });

  // inputsuserNamepasswordpassword2
  const { userName, password, password2 } = inputs;

  // getLogo()logo
  const logo = getLogo();

  // URL'aff'localStorage
  let affCode = new URLSearchParams(window.location.search).get('aff');
  if (affCode) {
    localStorage.setItem('aff', affCode);
  }

  useEffect(() => {
    // localStoragestatus
    let status = localStorage.getItem('status');
    if (status) {
      // status
      status = JSON.parse(status);
      // statusemail_verificationshowEmailVerification
      setShowEmailVerification(status.emailVerificationEnabled);
      // statusturnstile_checktrueturnstileEnabledtrueturnstileSiteKeystatusturnstile_site_key
      if (status.turnstileCheckEnabled) {
        setTurnstileEnabled(true);
        setTurnstileSiteKey(status.turnstileSiteKey);
      }
    }
  });

  /**
   * 
   * @param {Object} e - 
   */
  function handleChange(e) {
    // namevalue
    const { name, value } = e.target;
    console.log(name, value); // namevalue
    // inputsstate
    setInputs((inputs) => ({ ...inputs, [name]: value }));
  }

  /**
   * 
   * @param {Object} e - 
   */
  async function handleSubmit(e) {
    if (password.length < 8) {
      showInfo(' 8 '); // 
      return;
    }
    if (password !== password2) {
      showInfo(''); // 
      return;
    }
    if (userName && password) {
      if (turnstileEnabled && turnstileToken === '') {
        showInfo('Turnstile '); // 
        return;
      }
      setLoading(true); // loadingtrue
      if (!affCode) {
        affCode = localStorage.getItem('aff');
      }
      inputs.affCode = affCode; // affCodeinputs
      const res = await API.post(
        `/api/user/register?turnstile=${turnstileToken}`,
        inputs
      ); // 
      const { success, message } = res.data;
      if (success) {
        navigate('/login'); // 
        showSuccess(''); // 
      } else {
        showError(message); // 
      }
      setLoading(false); // loadingfalse
    }
  }

  /**
   * 
   */
  const sendVerificationCode = async () => {
    if (inputs.email === '') return; // 
    if (turnstileEnabled && turnstileToken === '') {
      showInfo('Turnstile '); // 
      return;
    }
    setLoading(true); // loadingtrue
    const res = await API.get(
      `/api/verification?email=${inputs.email}&turnstile=${turnstileToken}`
    ); // 
    const { success, message } = res.data;
    if (success) {
      showSuccess(''); // 
    } else {
      showError(message); // 
    }
    setLoading(false); // loadingfalse
  };

  return (
    <Grid textAlign='center' style={{ marginTop: '48px' }}>
      <Grid.Column style={{ maxWidth: 450 }}>
        <Header as='h2' color='' textAlign='center'>
          <Image src={logo} /> 
        </Header>
        <Form size='large'>
          <Segment>
            <Form.Input
              fluid
              icon='user'
              iconPosition='left'
              placeholder=' 12 '
              onChange={handleChange}
              name='userName'
            />
            <Form.Input
              fluid
              icon='lock'
              iconPosition='left'
              placeholder=' 8  20 '
              onChange={handleChange}
              name='password'
              type='password'
            />
            <Form.Input
              fluid
              icon='lock'
              iconPosition='left'
              placeholder=' 8  20 '
              onChange={handleChange}
              name='password2'
              type='password'
            />
            {showEmailVerification ? (
              <>
                <Form.Input
                  fluid
                  icon='mail'
                  iconPosition='left'
                  placeholder=''
                  onChange={handleChange}
                  name='email'
                  type='email'
                  action={
                    <Button onClick={sendVerificationCode} disabled={loading}>
                      
                    </Button>
                  }
                />
                <Form.Input
                  fluid
                  icon='lock'
                  iconPosition='left'
                  placeholder=''
                  onChange={handleChange}
                  name='verificationCode'
                />
              </>
            ) : (
              <></>
            )}
            {turnstileEnabled ? (
              <Turnstile
                sitekey={turnstileSiteKey}
                onVerify={(token) => {
                  setTurnstileToken(token);
                }}
              />
            ) : (
              <></>
            )}
            <Button
              color='green'
              fluid
              size='large'
              onClick={handleSubmit}
              loading={loading}
            >
              
            </Button>
          </Segment>
        </Form>
        <Message>
          
          <Link to='/login' className='btn btn-link'>
            
          </Link>
        </Message>
      </Grid.Column>
    </Grid>
  );
};

export default RegisterForm;
