import React, { useEffect, useState } from 'react';
import {
  Button,
  Divider,
  Form,
  Grid,
  Header,
  Message,
  Modal,
} from 'semantic-ui-react';
import { Link } from 'react-router-dom';
import { marked } from 'marked';

import { API, showError, showSuccess } from '../helpers';

const OtherSetting = () => {
  // useStateloadingfalse
  let [loading, setLoading] = useState(false);

  // useStateshowUpdateModalfalse
  const [showUpdateModal, setShowUpdateModal] = useState(false);

  // useStateinputs
  let [inputs, setInputs] = useState({
    Logo: '', // Logo
    Theme: '', // 
    Footer: '', // 
    Notice: '', // 
    About: '', // 
    SystemName: '', // 
    HomePageContent: '', // 
  });

  // useStateupdateDatatag_namecontent
  const [updateData, setUpdateData] = useState({
    tagName: '', // 
    content: '', // 
  });

  /**
   * APIinputs
   * @returns {Promise<void>}
   */
  const getOptions = async () => {
    // API
    const res = await API.get('/api/option/');

    // APIsuccessmessagedata
    const { success, message, data } = res.data;

    // 
    if (success) {
      // inputs
      let newInputs = {};

      // API
      data.forEach((item) => {
        // keyinputs
        if (item.key in inputs) {
          // newInputs
          newInputs[item.key] = item.value;
        }
      });

      // inputs
      setInputs(newInputs);
    } else {
      // 
      showError(message);
    }
  };

  // getOptions
  useEffect(() => {
    getOptions().then();
  }, []);

  /**
   * 
   * @param {string} key - 
   * @param {any} value - 
   * @returns {Promise<void>}
   */
  const updateOption = async (key, value) => {
    setLoading(true);

    // API
    const res = await API.put('/api/option/', {
      key,
      value,
    });

    // APIsuccessmessage
    const { success, message } = res.data;

    if (success) {
      // inputsinputs
      setInputs((inputs) => ({ ...inputs, [key]: value }));
    } else {
      // 
      showError(message);
    }

    setLoading(false);
  };

  /**
   * 
   * @param {Event} e - 
   * @param {Object} data - (name)(value)
   */
  const handleInputChange = async (e, { name, value }) => {
    // inputsinputs
    setInputs((inputs) => ({ ...inputs, [name]: value }));
  };
  /**
   * 
   */
  const submitNotice = async () => {
    await updateOption('Notice', inputs.Notice);
  };

  /**
   * 
   */
  const submitFooter = async () => {
    await updateOption('Footer', inputs.Footer);
  };

  /**
   * 
   */
  const submitSystemName = async () => {
    await updateOption('SystemName', inputs.SystemName);
  };

  /**
   * 
   */
  const submitTheme = async () => {
    await updateOption('Theme', inputs.Theme);
  };

  /**
   * Logo
   */
  const submitLogo = async () => {
    await updateOption('Logo', inputs.Logo);
  };

  /**
   * 
   */
  const submitAbout = async () => {
    await updateOption('About', inputs.About);
  };

  /**
   * 
   * @param {string} key - 
   */
  const submitOption = async (key) => {
    await updateOption(key, inputs[key]);
  };

  /**
   *  GitHub Release 
   */
  const openGitHubRelease = () => {
    //  window.location 
    window.location = 'https://gitee.com/zhongjyuan/one-api/releases/latest';
  };

  /**
   * 
   */
  const checkUpdate = async () => {
    try {
      // API
      const res = await API.get(
        'https://api.github.com/repos/songquanpeng/one-api/releases/latest'
      );
      const { tagName, body } = res.data;

      // 
      if (tagName === process.env.REACT_APP_VERSION) {
        // 
        showSuccess(`${tagName}`);
      } else {
        // 
        setUpdateData({
          tagName: tagName,
          content: marked.parse(body),
        });
        setShowUpdateModal(true);
      }
    } catch (error) {
      console.error('Error checking for updates:', error);
    }
  };

  return (
    <Grid columns={1}>
      <Grid.Column>
        <Form loading={loading}>
          <Header as='h3'></Header>
          <Form.Button onClick={checkUpdate}></Form.Button>
          <Form.Group widths='equal'>
            <Form.TextArea
              label=''
              placeholder=' Markdown & HTML '
              value={inputs.Notice}
              name='Notice'
              onChange={handleInputChange}
              style={{ minHeight: 150, fontFamily: 'JetBrains Mono, Consolas' }}
            />
          </Form.Group>
          <Form.Button onClick={submitNotice}></Form.Button>
          <Divider />
          <Header as='h3'></Header>
          <Form.Group widths='equal'>
            <Form.Input
              label=''
              placeholder=''
              value={inputs.SystemName}
              name='SystemName'
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Button onClick={submitSystemName}></Form.Button>
          <Form.Group widths='equal'>
            <Form.Input
              label={
                <label>
                  
                  <Link to='https://gitee.com/zhongjyuan/one-api/blob/main/web/README.md'>
                    
                  </Link>
                  
                </label>
              }
              placeholder=''
              value={inputs.Theme}
              name='Theme'
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Button onClick={submitTheme}></Form.Button>
          <Form.Group widths='equal'>
            <Form.Input
              label='Logo '
              placeholder=' Logo '
              value={inputs.Logo}
              name='Logo'
              type='url'
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Button onClick={submitLogo}> Logo</Form.Button>
          <Form.Group widths='equal'>
            <Form.TextArea
              label=''
              placeholder=' Markdown & HTML  iframe  src '
              value={inputs.HomePageContent}
              name='HomePageContent'
              onChange={handleInputChange}
              style={{ minHeight: 150, fontFamily: 'JetBrains Mono, Consolas' }}
            />
          </Form.Group>
          <Form.Button onClick={() => submitOption('HomePageContent')}>
            
          </Form.Button>
          <Form.Group widths='equal'>
            <Form.TextArea
              label=''
              placeholder=' Markdown & HTML  iframe  src '
              value={inputs.About}
              name='About'
              onChange={handleInputChange}
              style={{ minHeight: 150, fontFamily: 'JetBrains Mono, Consolas' }}
            />
          </Form.Group>
          <Form.Button onClick={submitAbout}></Form.Button>
          <Message>
             One API
            
          </Message>
          <Form.Group widths='equal'>
            <Form.Input
              label=''
              placeholder=' HTML '
              value={inputs.Footer}
              name='Footer'
              onChange={handleInputChange}
            />
          </Form.Group>
          <Form.Button onClick={submitFooter}></Form.Button>
        </Form>
      </Grid.Column>
      <Modal
        onClose={() => setShowUpdateModal(false)}
        onOpen={() => setShowUpdateModal(true)}
        open={showUpdateModal}
      >
        <Modal.Header>{updateData.tagName}</Modal.Header>
        <Modal.Content>
          <Modal.Description>
            <div dangerouslySetInnerHTML={{ __html: updateData.content }}></div>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button onClick={() => setShowUpdateModal(false)}></Button>
          <Button
            content=''
            onClick={() => {
              setShowUpdateModal(false);
              openGitHubRelease();
            }}
          />
        </Modal.Actions>
      </Modal>
    </Grid>
  );
};

export default OtherSetting;
