import React, { useEffect, useState } from 'react';
import {
  Button,
  Form,
  Input,
  Label,
  Message,
  Pagination,
  Popup,
  Table,
} from 'semantic-ui-react';
import { Link } from 'react-router-dom';
import {
  API,
  setPromptShown,
  shouldShowPrompt,
  showError,
  showInfo,
  showSuccess,
  timestamp2string,
} from '../helpers';

import { renderGroup, renderNumber } from '../helpers/render';

import { CHANNEL_OPTIONS, ITEMS_PER_PAGE } from '../constants';

/**
 * 
 * @param {number} timestamp - 
 * @returns {JSX.Element} 
 */
function renderTimestamp(timestamp) {
  //  timestamp2string(timestamp) 
  return <>{timestamp2string(timestamp)}</>;
}

let type2label = undefined;

/**
 * 
 * @param {number} type - 
 * @returns {JSX.Element} 
 */
function renderType(type) {
  if (!type2label) {
    type2label = new Map();

    for (let i = 0; i < CHANNEL_OPTIONS.length; i++) {
      type2label[CHANNEL_OPTIONS[i].value] = CHANNEL_OPTIONS[i];
    }

    type2label[0] = { value: 0, text: '', color: 'grey' };
  }

  return (
    <Label basic color={type2label[type]?.color}>
      {type2label[type]?.text}
    </Label>
  );
}

/**
 * 
 * @param {number} type - 
 * @param {number} balance - 
 * @returns {JSX.Element} 
 */
function renderBalance(type, balance) {
  switch (type) {
    case 1: // OpenAI
      return <span>${balance.toFixed(2)}</span>;
    case 4: // CloseAI
      return <span>{balance.toFixed(2)}</span>;
    case 8: // 
      return <span>${balance.toFixed(2)}</span>;
    case 5: // OpenAI-SB
      return <span>{(balance / 10000).toFixed(2)}</span>;
    case 10: // AI Proxy
      return <span>{renderNumber(balance)}</span>;
    case 12: // API2GPT
      return <span>{balance.toFixed(2)}</span>;
    case 13: // AIGC2D
      return <span>{renderNumber(balance)}</span>;
    default:
      return <span></span>;
  }
}

const ChannelsTable = () => {
  const [channels, setChannels] = useState([]); // 
  const [loading, setLoading] = useState(true); // 
  const [activePage, setActivePage] = useState(1); // 
  const [searchKeyword, setSearchKeyword] = useState(''); // 
  const [searching, setSearching] = useState(false); // 
  const [updatingBalance, setUpdatingBalance] = useState(false); // 
  const [showPrompt, setShowPrompt] = useState(
    shouldShowPrompt('channel-test')
  ); // 

  /**
   * 
   * @param {number} startIdx - 
   */
  const loadChannels = async (startIdx) => {
    //  API 
    const res = await API.get(`/api/channel/?p=${startIdx}`);
    const { success, message, data } = res.data;

    if (success) {
      if (startIdx === 0) {
        //  startIdx  0 channels 
        setChannels(data);
      } else {
        //  startIdx  0 channels 
        let newChannels = [...channels];
        newChannels.splice(startIdx * ITEMS_PER_PAGE, data.length, ...data);
        setChannels(newChannels);
      }
    } else {
      // 
      showError(message);
    }

    //  loading  false
    setLoading(false);
  };

  /**
   * 
   *  loadChannels 
   * @param {object} e - 
   * @param {object} data -  activePage 
   */
  const onPaginationChange = (e, { activePage }) => {
    //  async  await 
    (async () => {
      if (activePage === Math.ceil(channels.length / ITEMS_PER_PAGE) + 1) {
        // 
        await loadChannels(activePage - 1); //  loadChannels  channels 
      }
      setActivePage(activePage); // 
    })();
  };

  /**
   * 
   *  loading  true loadChannels 
   */
  const refresh = async () => {
    setLoading(true); //  loading  true
    await loadChannels(activePage - 1); //  loadChannels 
  };

  //  useEffect 
  useEffect(() => {
    // 
    loadChannels(0)
      .then()
      .catch((reason) => {
        // 
        showError(reason);
      });
  }, []);

  /**
   * 
   * @param {string} id - 
   * @param {string} action -  'delete''enable''disable''priority'  'weight'
   * @param {number} idx - 
   * @param {string} value - 
   */
  const manageChannel = async (id, action, idx, value) => {
    // 
    let data = { id };
    let res;

    // 
    switch (action) {
      case 'delete':
        // 
        res = await API.delete(`/api/channel/${id}/`);
        break;
      case 'enable':
        // 
        data.status = 1;
        res = await API.put('/api/channel/', data);
        break;
      case 'disable':
        // 
        data.status = 2;
        res = await API.put('/api/channel/', data);
        break;
      case 'priority':
        // 
        if (value === '') {
          return;
        }
        data.priority = parseInt(value);
        res = await API.put('/api/channel/', data);
        break;
      case 'weight':
        // 
        if (value === '') {
          return;
        }
        data.weight = parseInt(value);
        if (data.weight < 0) {
          data.weight = 0;
        }
        res = await API.put('/api/channel/', data);
        break;
    }

    //  API 
    const { success, message } = res.data;

    // 
    if (success) {
      showSuccess('');
      let channel = res.data.data;
      let newChannels = [...channels];
      let realIdx = (activePage - 1) * ITEMS_PER_PAGE + idx;

      if (action === 'delete') {
        newChannels[realIdx].deleted = true;
      } else {
        newChannels[realIdx].status = channel.status;
      }

      setChannels(newChannels);
    } else {
      showError(message);
    }
  };

  /**
   * 
   * @param {number} status -  123
   * @returns {JSX.Element} - 
   */
  const renderStatus = (status) => {
    switch (status) {
      case 1:
        //  1 
        return (
          <Label basic color='green'>
            
          </Label>
        );
      case 2:
        //  2 
        return (
          <Popup
            trigger={
              <Label basic color='red'>
                
              </Label>
            }
            content=''
            basic
          />
        );
      case 3:
        //  3 
        return (
          <Popup
            trigger={
              <Label basic color='yellow'>
                
              </Label>
            }
            content=''
            basic
          />
        );
      default:
        // 
        return (
          <Label basic color='grey'>
            
          </Label>
        );
    }
  };

  /**
   * 
   * @param {number} responseTime - 
   * @returns {JSX.Element} - 
   */
  const renderResponseTime = (responseTime) => {
    let time = responseTime / 1000; // 
    time = time.toFixed(2) + ' '; // 

    if (responseTime === 0) {
      //  0 
      return (
        <Label basic color='grey'>
          
        </Label>
      );
    } else if (responseTime <= 1000) {
      //  1000 
      return (
        <Label basic color='green'>
          {time}
        </Label>
      );
    } else if (responseTime <= 3000) {
      //  3000 
      return (
        <Label basic color='olive'>
          {time}
        </Label>
      );
    } else if (responseTime <= 5000) {
      //  5000 
      return (
        <Label basic color='yellow'>
          {time}
        </Label>
      );
    } else {
      // 
      return (
        <Label basic color='red'>
          {time}
        </Label>
      );
    }
  };

  /**
   * 
   */
  const searchChannels = async () => {
    if (searchKeyword === '') {
      // 
      await loadChannels(0); //  loadChannels 
      setActivePage(1); // 
      return;
    }

    setSearching(true); //  true
    const res = await API.get(`/api/channel/search?keyword=${searchKeyword}`); // 
    const { success, message, data } = res.data; //  successmessage  data

    if (success) {
      setChannels(data); // 
      setActivePage(1); // 
    } else {
      showError(message); // 
    }

    setSearching(false); //  false
  };

  /**
   * 
   * @param {string} id - 
   * @param {string} name - 
   * @param {number} idx - 
   */
  const testChannel = async (id, name, idx) => {
    const res = await API.get(`/api/channel/test/${id}/`); // 
    const { success, message, time } = res.data; //  successmessage  time

    if (success) {
      let newChannels = [...channels]; // 
      let realIdx = (activePage - 1) * ITEMS_PER_PAGE + idx; // 
      newChannels[realIdx].responseTime = time * 1000; // 
      newChannels[realIdx].testTime = Date.now() / 1000; // 
      setChannels(newChannels); // 
      showInfo(` ${name}  ${time.toFixed(2)} `); // 
    } else {
      showError(message); // 
    }
  };

  /**
   * 
   */
  const testAllChannels = async () => {
    const res = await API.get(`/api/channel/test`); // 
    const { success, message } = res.data; //  success  message

    if (success) {
      showInfo(''); // 
    } else {
      showError(message); // 
    }
  };

  /**
   * 
   */
  const deleteAllDisabledChannels = async () => {
    const res = await API.delete(`/api/channel/disabled`); // 
    const { success, message, data } = res.data; //  successmessage  data

    if (success) {
      showSuccess(` ${data} `); // 
      await refresh(); // 
    } else {
      showError(message); // 
    }
  };

  /**
   * 
   * @param {string} id - 
   * @param {string} name - 
   * @param {number} idx - 
   */
  const updateChannelBalance = async (id, name, idx) => {
    const res = await API.get(`/api/channel/update_balance/${id}/`); // 
    const { success, message, balance } = res.data; //  successmessage  balance

    if (success) {
      let newChannels = [...channels]; // 
      let realIdx = (activePage - 1) * ITEMS_PER_PAGE + idx; // 
      newChannels[realIdx].balance = balance; // 
      newChannels[realIdx].balanceUpdateTime = Date.now() / 1000; // 
      setChannels(newChannels); // 
      showInfo(` ${name} `); // 
    } else {
      showError(message); // 
    }
  };

  /**
   * 
   */
  const updateAllChannelsBalance = async () => {
    setUpdatingBalance(true); //  true
    const res = await API.get(`/api/channel/update_balance`); // 
    const { success, message } = res.data; //  success  message

    if (success) {
      showInfo(''); // 
    } else {
      showError(message); // 
    }
    setUpdatingBalance(false); //  false
  };

  /**
   * 
   * @param {Object} e - 
   * @param {Object} value - 
   */
  const handleKeywordChange = async (e, { value }) => {
    setSearchKeyword(value.trim()); // 
  };

  /**
   * 
   * @param {string} key - 
   */
  const sortChannel = (key) => {
    if (channels.length === 0) return; // 
    setLoading(true); //  true
    let sortedChannels = [...channels]; // 
    sortedChannels.sort((a, b) => {
      if (!isNaN(a[key])) {
        // 
        return a[key] - b[key];
      } else {
        // 
        return ('' + a[key]).localeCompare(b[key]);
      }
    });
    if (sortedChannels[0].id === channels[0].id) {
      sortedChannels.reverse(); // 
    }
    setChannels(sortedChannels); // 
    setLoading(false); //  false
  };

  return (
    <>
      <Form onSubmit={searchChannels}>
        <Form.Input
          icon='search'
          fluid
          iconPosition='left'
          placeholder=' ID ...'
          value={searchKeyword}
          loading={searching}
          onChange={handleKeywordChange}
        />
      </Form>
      {showPrompt && (
        <Message
          onDismiss={() => {
            setShowPrompt(false);
            setPromptShown('channel-test');
          }}
        >
          OpenAI  key 
          0
        </Message>
      )}
      <Table basic compact size='small'>
        <Table.Header>
          <Table.Row>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortChannel('id');
              }}
            >
              ID
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortChannel('name');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortChannel('group');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortChannel('type');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortChannel('status');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortChannel('responseTime');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortChannel('balance');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell
              style={{ cursor: 'pointer' }}
              onClick={() => {
                sortChannel('priority');
              }}
            >
              
            </Table.HeaderCell>
            <Table.HeaderCell></Table.HeaderCell>
          </Table.Row>
        </Table.Header>

        <Table.Body>
          {channels
            .slice(
              (activePage - 1) * ITEMS_PER_PAGE,
              activePage * ITEMS_PER_PAGE
            )
            .map((channel, idx) => {
              if (channel.deleted) return <></>;
              return (
                <Table.Row key={channel.id}>
                  <Table.Cell>{channel.id}</Table.Cell>
                  <Table.Cell>{channel.name ? channel.name : ''}</Table.Cell>
                  <Table.Cell>{renderGroup(channel.group)}</Table.Cell>
                  <Table.Cell>{renderType(channel.type)}</Table.Cell>
                  <Table.Cell>{renderStatus(channel.status)}</Table.Cell>
                  <Table.Cell>
                    <Popup
                      content={
                        channel.testTime
                          ? renderTimestamp(channel.testTime)
                          : ''
                      }
                      key={channel.id}
                      trigger={renderResponseTime(channel.responseTime)}
                      basic
                    />
                  </Table.Cell>
                  <Table.Cell>
                    <Popup
                      trigger={
                        <span
                          onClick={() => {
                            updateChannelBalance(channel.id, channel.name, idx);
                          }}
                          style={{ cursor: 'pointer' }}
                        >
                          {renderBalance(channel.type, channel.balance)}
                        </span>
                      }
                      content=''
                      basic
                    />
                  </Table.Cell>
                  <Table.Cell>
                    <Popup
                      trigger={
                        <Input
                          type='number'
                          defaultValue={channel.priority}
                          onBlur={(event) => {
                            manageChannel(
                              channel.id,
                              'priority',
                              idx,
                              event.target.value
                            );
                          }}
                        >
                          <input style={{ maxWidth: '60px' }} />
                        </Input>
                      }
                      content=''
                      basic
                    />
                  </Table.Cell>
                  <Table.Cell>
                    <div>
                      <Button
                        size={'small'}
                        positive
                        onClick={() => {
                          testChannel(channel.id, channel.name, idx);
                        }}
                      >
                        
                      </Button>
                      <Button
                        size={'small'}
                        positive
                        loading={updatingBalance}
                        onClick={() => {
                          updateChannelBalance(channel.id, channel.name, idx);
                        }}
                      >
                        
                      </Button>
                      <Popup
                        trigger={
                          <Button size='small' negative>
                            
                          </Button>
                        }
                        on='click'
                        flowing
                        hoverable
                      >
                        <Button
                          negative
                          onClick={() => {
                            manageChannel(channel.id, 'delete', idx);
                          }}
                        >
                           {channel.name}
                        </Button>
                      </Popup>
                      <Button
                        size={'small'}
                        onClick={() => {
                          manageChannel(
                            channel.id,
                            channel.status === 1 ? 'disable' : 'enable',
                            idx
                          );
                        }}
                      >
                        {channel.status === 1 ? '' : ''}
                      </Button>
                      <Button
                        size={'small'}
                        as={Link}
                        to={'/channel/edit/' + channel.id}
                      >
                        
                      </Button>
                    </div>
                  </Table.Cell>
                </Table.Row>
              );
            })}
        </Table.Body>

        <Table.Footer>
          <Table.Row>
            <Table.HeaderCell colSpan='9'>
              <Button
                size='small'
                as={Link}
                to='/channel/add'
                loading={loading}
              >
                
              </Button>
              <Button size='small' loading={loading} onClick={testAllChannels}>
                
              </Button>
              <Button
                size='small'
                onClick={updateAllChannelsBalance}
                loading={loading || updatingBalance}
              >
                
              </Button>
              <Popup
                trigger={
                  <Button size='small' loading={loading}>
                    
                  </Button>
                }
                on='click'
                flowing
                hoverable
              >
                <Button
                  size='small'
                  loading={loading}
                  negative
                  onClick={deleteAllDisabledChannels}
                >
                  
                </Button>
              </Popup>
              <Pagination
                floated='right'
                activePage={activePage}
                onPageChange={onPaginationChange}
                size='small'
                siblingRange={1}
                totalPages={
                  Math.ceil(channels.length / ITEMS_PER_PAGE) +
                  (channels.length % ITEMS_PER_PAGE === 0 ? 1 : 0)
                }
              />
              <Button size='small' onClick={refresh} loading={loading}>
                
              </Button>
            </Table.HeaderCell>
          </Table.Row>
        </Table.Footer>
      </Table>
    </>
  );
};

export default ChannelsTable;
