import React, { useContext, useEffect, useState } from 'react';
import {
  Button,
  Divider,
  Form,
  Grid,
  Header,
  Image,
  Message,
  Modal,
  Segment,
} from 'semantic-ui-react';
import { Link, useNavigate, useSearchParams } from 'react-router-dom';

import { UserContext } from '../context/User';
import { API, getLogo, showError, showSuccess, showWarning } from '../helpers';
import { onGitHubOAuthClicked } from './utils';

const LoginForm = () => {
  // 
  let navigate = useNavigate();

  const [status, setStatus] = useState({}); // 
  const [submitted, setSubmitted] = useState(false); // 
  const [showWeChatLoginModal, setShowWeChatLoginModal] = useState(false); // 
  const [inputs, setInputs] = useState({
    userName: '',
    password: '',
    wechatVverificationCode: '',
  }); // 
  const { userName, password } = inputs; // 

  const [userState, userDispatch] = useContext(UserContext); // 
  const [searchParams, setSearchParams] = useSearchParams(); //  URL 

  //  logo 
  const logo = getLogo();

  useEffect(() => {
    //  URL  'expired'
    if (searchParams.get('expired')) {
      showError('');
    }

    //  localStorage 
    let status = localStorage.getItem('status');
    if (status) {
      status = JSON.parse(status);
      setStatus(status);
    }
  }, []);

  // 
  const onWeChatLoginClicked = () => {
    setShowWeChatLoginModal(true);
  };

  /**
   * 
   */
  const onSubmitWeChatVerificationCode = async () => {
    //  API 
    const res = await API.get(
      `/api/oauth/wechat?code=${inputs.wechatVverificationCode}`
    );
    const { success, message, data } = res.data;

    if (success) {
      //  localStorage
      userDispatch({ type: 'login', payload: data });
      localStorage.setItem('user', JSON.stringify(data));
      navigate('/'); // 
      showSuccess(''); // 
      setShowWeChatLoginModal(false); // 
    } else {
      // 
      showError(message);
    }
  };

  /**
   * 
   * @param {Object} e - 
   */
  function handleChange(e) {
    const { name, value } = e.target;
    setInputs((inputs) => ({ ...inputs, [name]: value }));
  }

  /**
   * 
   * @param {Object} e - 
   */
  async function handleSubmit(e) {
    setSubmitted(true); //  true

    if (userName && password) {
      // 
      const res = await API.post(`/api/user/login`, {
        // 
        userName,
        password,
      });

      const { success, message, data } = res.data; // 

      if (success) {
        // 
        userDispatch({ type: 'login', payload: data }); // 
        localStorage.setItem('user', JSON.stringify(data)); //  localStorage

        if (userName === 'root' && password === '123456') {
          // 
          navigate('/user/edit'); // 
          showSuccess(''); // 
          showWarning(''); // 
        } else {
          // 
          navigate('/token'); //  token 
          showSuccess(''); // 
        }
      } else {
        // 
        showError(message); // 
      }
    }
  }

  return (
    <Grid textAlign='center' style={{ marginTop: '48px' }}>
      <Grid.Column style={{ maxWidth: 450 }}>
        <Header as='h2' color='' textAlign='center'>
          <Image src={logo} /> 
        </Header>
        <Form size='large'>
          <Segment>
            <Form.Input
              fluid
              icon='user'
              iconPosition='left'
              placeholder=''
              name='userName'
              value={userName}
              onChange={handleChange}
            />
            <Form.Input
              fluid
              icon='lock'
              iconPosition='left'
              placeholder=''
              name='password'
              type='password'
              value={password}
              onChange={handleChange}
            />
            <Button color='green' fluid size='large' onClick={handleSubmit}>
              
            </Button>
          </Segment>
        </Form>
        <Message>
          
          <Link to='/reset' className='btn btn-link'>
            
          </Link>
           
          <Link to='/register' className='btn btn-link'>
            
          </Link>
        </Message>
        {status.gitHubOAuthEnabled || status.weChatAuthEnabled ? (
          <>
            <Divider horizontal>Or</Divider>
            {status.gitHubOAuthEnabled ? (
              <Button
                circular
                color='black'
                icon='github'
                onClick={() => onGitHubOAuthClicked(status.gitHubClientId)}
              />
            ) : (
              <></>
            )}
            {status.weChatAuthEnabled ? (
              <Button
                circular
                color='green'
                icon='wechat'
                onClick={onWeChatLoginClicked}
              />
            ) : (
              <></>
            )}
          </>
        ) : (
          <></>
        )}
        <Modal
          onClose={() => setShowWeChatLoginModal(false)}
          onOpen={() => setShowWeChatLoginModal(true)}
          open={showWeChatLoginModal}
          size={'mini'}
        >
          <Modal.Content>
            <Modal.Description>
              <Image src={status.wechatQRcode} fluid />
              <div style={{ textAlign: 'center' }}>
                <p>
                  
                </p>
              </div>
              <Form size='large'>
                <Form.Input
                  fluid
                  placeholder=''
                  name='wechatVverificationCode'
                  value={inputs.wechatVverificationCode}
                  onChange={handleChange}
                />
                <Button
                  color=''
                  fluid
                  size='large'
                  onClick={onSubmitWeChatVerificationCode}
                >
                  
                </Button>
              </Form>
            </Modal.Description>
          </Modal.Content>
        </Modal>
      </Grid.Column>
    </Grid>
  );
};

export default LoginForm;
