import React, { useContext, useState } from 'react';
import {
  Button,
  Container,
  Dropdown,
  Icon,
  Menu,
  Segment,
} from 'semantic-ui-react';
import { Link, useNavigate } from 'react-router-dom';

import {
  API,
  getLogo,
  getSystemName,
  isAdmin,
  isMobile,
  showSuccess,
} from '../helpers';
import { UserContext } from '../context/User';

import '../index.css';

// Header Buttons// 
let headerButtons = [
  {
    name: '',
    to: '/',
    icon: 'home',
  },
  {
    name: '',
    to: '/channel',
    icon: 'sitemap',
    admin: true, // 
  },
  {
    name: '',
    to: '/token',
    icon: 'key',
  },
  {
    name: '',
    to: '/redemption',
    icon: 'dollar sign',
    admin: true, // 
  },
  {
    name: '',
    to: '/topup',
    icon: 'cart',
  },
  {
    name: '',
    to: '/user',
    icon: 'user',
    admin: true, // 
  },
  {
    name: '',
    to: '/log',
    icon: 'book',
  },
  {
    name: '',
    to: '/setting',
    icon: 'setting',
  },
  {
    name: '',
    to: '/about',
    icon: 'info circle',
  },
];

//  'chatLink'
if (localStorage.getItem('chatLink')) {
  //  'chatLink' headerButtons 
  headerButtons.splice(1, 0, {
    name: '',
    to: '/chat',
    icon: 'comments',
  });
}

const Header = () => {
  //  useNavigate Hook 
  let navigate = useNavigate();

  //  useState Hook  showSidebar  setShowSidebar
  const [showSidebar, setShowSidebar] = useState(false);

  //  useContext Hook  UserContext 
  const [userState, userDispatch] = useContext(UserContext);

  //  getLogo()  getSystemName()  logo 
  const logo = getLogo();
  const systemName = getSystemName();

  /**
   * 
   */
  async function logout() {
    // 
    setShowSidebar(false);

    // API
    await API.get('/api/user/logout');

    // 
    showSuccess('!');

    // 
    userDispatch({ type: 'logout' });

    // 
    localStorage.removeItem('user');

    // 
    navigate('/login');
  }

  /**
   * 
   */
  const toggleSidebar = () => {
    //  showSidebar 
    setShowSidebar(!showSidebar);
  };

  /**
   *  isMobile 
   * @param {boolean} isMobile - 
   * @returns {Array} 
   */
  const renderButtons = (isMobile) => {
    return headerButtons.map((button) => {
      // 
      if (button.admin && !isAdmin()) return <></>;

      if (isMobile) {
        // 
        return (
          <Menu.Item
            onClick={() => {
              navigate(button.to);
              setShowSidebar(false);
            }}
          >
            {button.name}
          </Menu.Item>
        );
      } else {
        // 
        return (
          <Menu.Item key={button.name} as={Link} to={button.to}>
            <Icon name={button.icon} />
            {button.name}
          </Menu.Item>
        );
      }
    });
  };

  if (isMobile()) {
    return (
      <>
        <Menu
          borderless
          size='large'
          style={
            showSidebar
              ? {
                  borderBottom: 'none',
                  marginBottom: '0',
                  borderTop: 'none',
                  height: '51px',
                }
              : { borderTop: 'none', height: '52px' }
          }
        >
          <Container>
            <Menu.Item as={Link} to='/'>
              <img src={logo} alt='logo' style={{ marginRight: '0.75em' }} />
              <div style={{ fontSize: '20px' }}>
                <b>{systemName}</b>
              </div>
            </Menu.Item>
            <Menu.Menu position='right'>
              <Menu.Item onClick={toggleSidebar}>
                <Icon name={showSidebar ? 'close' : 'sidebar'} />
              </Menu.Item>
            </Menu.Menu>
          </Container>
        </Menu>
        {showSidebar ? (
          <Segment style={{ marginTop: 0, borderTop: '0' }}>
            <Menu secondary vertical style={{ width: '100%', margin: 0 }}>
              {renderButtons(true)}
              <Menu.Item>
                {userState.user ? (
                  <Button onClick={logout}></Button>
                ) : (
                  <>
                    <Button
                      onClick={() => {
                        setShowSidebar(false);
                        navigate('/login');
                      }}
                    >
                      
                    </Button>
                    <Button
                      onClick={() => {
                        setShowSidebar(false);
                        navigate('/register');
                      }}
                    >
                      
                    </Button>
                  </>
                )}
              </Menu.Item>
            </Menu>
          </Segment>
        ) : (
          <></>
        )}
      </>
    );
  }

  return (
    <>
      <Menu borderless style={{ borderTop: 'none' }}>
        <Container>
          <Menu.Item as={Link} to='/' className={'hide-on-mobile'}>
            <img src={logo} alt='logo' style={{ marginRight: '0.75em' }} />
            <div style={{ fontSize: '20px' }}>
              <b>{systemName}</b>
            </div>
          </Menu.Item>
          {renderButtons(false)}
          <Menu.Menu position='right'>
            {userState.user ? (
              <Dropdown
                text={userState.user.userName}
                pointing
                className='link item'
              >
                <Dropdown.Menu>
                  <Dropdown.Item onClick={logout}></Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
            ) : (
              <Menu.Item
                name=''
                as={Link}
                to='/login'
                className='btn btn-link'
              />
            )}
          </Menu.Menu>
        </Container>
      </Menu>
    </>
  );
};

export default Header;
