import React, { useEffect, useState } from 'react';
import {
  Button,
  Form,
  Header,
  Label,
  Pagination,
  Segment,
  Select,
  Table,
} from 'semantic-ui-react';

import { API, isAdmin, showError, timestamp2string } from '../helpers';
import { renderQuota } from '../helpers/render';

import { ITEMS_PER_PAGE } from '../constants';

/**
 * UI
 *
 * @param {number} timestamp - 
 * @returns {JSX.Element} -  JSX 
 */
function renderTimestamp(timestamp) {
  return <>{timestamp2string(timestamp)}</>;
}

/**
 * 
 *
 * 
 *   - key: 
 *   - text: UI
 *   - value: 
 *
 * @type {Array<{key: string, text: string, value: string}>}
 */
const MODE_OPTIONS = [
  { key: 'all', text: '', value: 'all' },
  { key: 'self', text: '', value: 'self' },
];

/**
 * 
 *
 * 
 *   - key: 
 *   - text:  UI 
 *   - value: 
 *
 * @type {Array<{key: string, text: string, value: number}>}
 */
const LOG_OPTIONS = [
  { key: '0', text: '', value: 0 },
  { key: '1', text: '', value: 1 },
  { key: '2', text: '', value: 2 },
  { key: '3', text: '', value: 3 },
  { key: '4', text: '', value: 4 },
];

/**
 * 
 *
 * @param {number} type - 
 * @returns {JSX.Element} -  JSX 
 */
function renderType(type) {
  switch (type) {
    case 1:
      return (
        <Label basic color='green'>
          {' '}
          {' '}
        </Label>
      );
    case 2:
      return (
        <Label basic color='olive'>
          {' '}
          {' '}
        </Label>
      );
    case 3:
      return (
        <Label basic color='orange'>
          {' '}
          {' '}
        </Label>
      );
    case 4:
      return (
        <Label basic color='purple'>
          {' '}
          {' '}
        </Label>
      );
    default:
      return (
        <Label basic color='black'>
          {' '}
          {' '}
        </Label>
      );
  }
}

const LogsTable = () => {

  // 
  let now = new Date();
  // 
  const isAdminUser = isAdmin();
  
  const [loading, setLoading] = useState(true); // 
  const [showStat, setShowStat] = useState(false); // 
  const [searching, setSearching] = useState(false); // 
  const [logs, setLogs] = useState([]); // 
  const [logType, setLogType] = useState(0); // 
  const [activePage, setActivePage] = useState(1); // 
  const [searchKeyword, setSearchKeyword] = useState(''); // 

  const [stat, setStat] = useState({
    quota: 0, // 
    token: 0, // 
  });

  const [inputs, setInputs] = useState({
    userName: '', // 
    tokenName: '', // 
    modelName: '', // 
    channelId: '', // ID
    startTimestamp: timestamp2string(0), // 
    endTimestamp: timestamp2string(now.getTime() / 1000 + 3600), // 
  });

  //  inputs 
  const {
    userName,
    tokenName,
    modelName,
    startTimestamp,
    endTimestamp,
    channelId,
  } = inputs;

  /**
   * 
   *
   * @param {Event} e - .
   * @param {Object} data - .
   */
  const handleInputChange = (e, { name, value }) => {
    //  inputs 
    setInputs((inputs) => ({ ...inputs, [name]: value }));
  };

  /**
   * 
   */
  const getLogSelfStat = async () => {
    // 
    let localStartTimestamp = Date.parse(startTimestamp) / 1000;
    let localEndTimestamp = Date.parse(endTimestamp) / 1000;

    //  API 
    try {
      let res = await API.get(
        `/api/log/self/stat?type=${logType}&tokenName=${tokenName}&modelName=${modelName}&startTimestamp=${localStartTimestamp}&endTimestamp=${localEndTimestamp}`
      );

      const { success, message, data } = res.data;

      if (success) {
        // 
        setStat(data);
      } else {
        // 
        showError(message);
      }
    } catch (error) {
      // 
      console.error('Error fetching log self stat:', error);
    }
  };

  /**
   * 
   */
  const getLogStat = async () => {
    // 
    let localStartTimestamp = Date.parse(startTimestamp) / 1000;
    let localEndTimestamp = Date.parse(endTimestamp) / 1000;

    //  API 
    try {
      let res = await API.get(
        `/api/log/stat?type=${logType}&userName=${userName}&tokenName=${tokenName}&modelName=${modelName}&startTimestamp=${localStartTimestamp}&endTimestamp=${localEndTimestamp}&channelId=${channelId}`
      );

      const { success, message, data } = res.data;

      if (success) {
        // 
        setStat(data);
      } else {
        // 
        showError(message);
      }
    } catch (error) {
      // 
      console.error('Error fetching log stat:', error);
    }
  };

  /**
   * /
   */
  const handleEyeClick = async () => {
    if (!showStat) {
      // 
      if (isAdminUser) {
        // 
        await getLogStat();
      } else {
        // 
        await getLogSelfStat();
      }
    }

    // 
    setShowStat(!showStat);
  };

  /**
   * 
   * @param {number} startIdx - 
   */
  const loadLogs = async (startIdx) => {
    let url = '';
    let localStartTimestamp = Date.parse(startTimestamp) / 1000;
    let localEndTimestamp = Date.parse(endTimestamp) / 1000;

    //  URL
    if (isAdminUser) {
      url = `/api/log/?p=${startIdx}&type=${logType}&userName=${userName}&tokenName=${tokenName}&modelName=${modelName}&startTimestamp=${localStartTimestamp}&endTimestamp=${localEndTimestamp}&channelId=${channelId}`;
    } else {
      url = `/api/log/self/?p=${startIdx}&type=${logType}&tokenName=${tokenName}&modelName=${modelName}&startTimestamp=${localStartTimestamp}&endTimestamp=${localEndTimestamp}`;
    }

    //  API 
    const res = await API.get(url);
    const { success, message, data } = res.data;

    if (success) {
      if (startIdx === 0) {
        // 
        setLogs(data);
      } else {
        // 
        let newLogs = [...logs];
        newLogs.splice(startIdx * ITEMS_PER_PAGE, data.length, ...data);
        setLogs(newLogs);
      }
    } else {
      // 
      showError(message);
    }

    // 
    setLoading(false);
  };

  /**
   * 
   * @param {Event} e - 
   * @param {Object} data - 
   */
  const onPaginationChange = (e, { activePage }) => {
    (async () => {
      if (activePage === Math.ceil(logs.length / ITEMS_PER_PAGE) + 1) {
        // 
        await loadLogs(activePage - 1);
      }
      setActivePage(activePage);
    })();
  };

  /**
   * 
   */
  const refresh = async () => {
    setLoading(true); //  true
    setActivePage(1); //  1
    await loadLogs(0); // 
  };

  /**
   * 
   */
  useEffect(() => {
    refresh().then();
  }, [logType]);

  /**
   * 
   */
  const searchLogs = async () => {
    if (searchKeyword === '') {
      // 
      await loadLogs(0);
      setActivePage(1);
      return;
    }

    setSearching(true); //  true

    const res = await API.get(`/api/log/self/search?keyword=${searchKeyword}`);
    const { success, message, data } = res.data;

    if (success) {
      setLogs(data); // 
      setActivePage(1); // 
    } else {
      showError(message); // 
    }

    setSearching(false); //  false
  };

  /**
   * 
   * @param {Event} e - 
   * @param {Object} data - 
   */
  const handleKeywordChange = async (e, { value }) => {
    setSearchKeyword(value.trim()); // 
  };

  /**
   * 
   * @param {string} key - 
   */
  const sortLog = (key) => {
    if (logs.length === 0) return; // 

    setLoading(true); //  true

    let sortedLogs = [...logs]; // 

    // 
    if (typeof sortedLogs[0][key] === 'string') {
      sortedLogs.sort((a, b) => {
        return ('' + a[key]).localeCompare(b[key]);
      });
    } else {
      sortedLogs.sort((a, b) => {
        if (a[key] === b[key]) return 0;
        if (a[key] > b[key]) return -1;
        if (a[key] < b[key]) return 1;
      });
    }

    // 
    if (sortedLogs[0].id === logs[0].id) {
      sortedLogs.reverse();
    }

    setLogs(sortedLogs); // 
    setLoading(false); //  false
  };

  return (
    <>
      <Segment>
        <Header as='h3'>
          
          {showStat && renderQuota(stat.quota)}
          {!showStat && (
            <span
              onClick={handleEyeClick}
              style={{ cursor: 'pointer', color: 'gray' }}
            >
              
            </span>
          )}
          
        </Header>
        <Form>
          <Form.Group>
            <Form.Input
              fluid
              label={''}
              width={3}
              value={tokenName}
              placeholder={''}
              name='tokenName'
              onChange={handleInputChange}
            />
            <Form.Input
              fluid
              label=''
              width={3}
              value={modelName}
              placeholder=''
              name='modelName'
              onChange={handleInputChange}
            />
            <Form.Input
              fluid
              label=''
              width={4}
              value={startTimestamp}
              type='datetime-local'
              name='startTimestamp'
              onChange={handleInputChange}
            />
            <Form.Input
              fluid
              label=''
              width={4}
              value={endTimestamp}
              type='datetime-local'
              name='endTimestamp'
              onChange={handleInputChange}
            />
            <Form.Button fluid label='' width={2} onClick={refresh}>
              
            </Form.Button>
          </Form.Group>
          {isAdminUser && (
            <>
              <Form.Group>
                <Form.Input
                  fluid
                  label={' ID'}
                  width={3}
                  value={channelId}
                  placeholder=''
                  name='channelId'
                  onChange={handleInputChange}
                />
                <Form.Input
                  fluid
                  label={''}
                  width={3}
                  value={userName}
                  placeholder={''}
                  name='userName'
                  onChange={handleInputChange}
                />
              </Form.Group>
            </>
          )}
        </Form>
        <Table basic compact size='small'>
          <Table.Header>
            <Table.Row>
              <Table.HeaderCell
                style={{ cursor: 'pointer' }}
                onClick={() => {
                  sortLog('createTime');
                }}
                width={3}
              >
                
              </Table.HeaderCell>
              {isAdminUser && (
                <Table.HeaderCell
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    sortLog('channelId');
                  }}
                  width={1}
                >
                  
                </Table.HeaderCell>
              )}
              {isAdminUser && (
                <Table.HeaderCell
                  style={{ cursor: 'pointer' }}
                  onClick={() => {
                    sortLog('userName');
                  }}
                  width={1}
                >
                  
                </Table.HeaderCell>
              )}
              <Table.HeaderCell
                style={{ cursor: 'pointer' }}
                onClick={() => {
                  sortLog('tokenName');
                }}
                width={1}
              >
                
              </Table.HeaderCell>
              <Table.HeaderCell
                style={{ cursor: 'pointer' }}
                onClick={() => {
                  sortLog('type');
                }}
                width={1}
              >
                
              </Table.HeaderCell>
              <Table.HeaderCell
                style={{ cursor: 'pointer' }}
                onClick={() => {
                  sortLog('modelName');
                }}
                width={2}
              >
                
              </Table.HeaderCell>
              <Table.HeaderCell
                style={{ cursor: 'pointer' }}
                onClick={() => {
                  sortLog('promptTokens');
                }}
                width={1}
              >
                
              </Table.HeaderCell>
              <Table.HeaderCell
                style={{ cursor: 'pointer' }}
                onClick={() => {
                  sortLog('completionTokens');
                }}
                width={1}
              >
                
              </Table.HeaderCell>
              <Table.HeaderCell
                style={{ cursor: 'pointer' }}
                onClick={() => {
                  sortLog('quota');
                }}
                width={1}
              >
                
              </Table.HeaderCell>
              <Table.HeaderCell
                style={{ cursor: 'pointer' }}
                onClick={() => {
                  sortLog('content');
                }}
                width={isAdminUser ? 4 : 6}
              >
                
              </Table.HeaderCell>
            </Table.Row>
          </Table.Header>

          <Table.Body>
            {logs
              .slice(
                (activePage - 1) * ITEMS_PER_PAGE,
                activePage * ITEMS_PER_PAGE
              )
              .map((log, idx) => {
                if (log.deleted) return <></>;
                return (
                  <Table.Row key={log.id}>
                    <Table.Cell>{renderTimestamp(log.createTime)}</Table.Cell>
                    {isAdminUser && (
                      <Table.Cell>
                        {log.channelId ? (
                          <Label basic>{log.channelId}</Label>
                        ) : (
                          ''
                        )}
                      </Table.Cell>
                    )}
                    {isAdminUser && (
                      <Table.Cell>
                        {log.userName ? <Label>{log.userName}</Label> : ''}
                      </Table.Cell>
                    )}
                    <Table.Cell>
                      {log.tokenName ? (
                        <Label basic>{log.tokenName}</Label>
                      ) : (
                        ''
                      )}
                    </Table.Cell>
                    <Table.Cell>{renderType(log.type)}</Table.Cell>
                    <Table.Cell>
                      {log.modelName ? (
                        <Label basic>{log.modelName}</Label>
                      ) : (
                        ''
                      )}
                    </Table.Cell>
                    <Table.Cell>
                      {log.promptTokens ? log.promptTokens : ''}
                    </Table.Cell>
                    <Table.Cell>
                      {log.completionTokens ? log.completionTokens : ''}
                    </Table.Cell>
                    <Table.Cell>
                      {log.quota ? renderQuota(log.quota, 6) : ''}
                    </Table.Cell>
                    <Table.Cell>{log.content}</Table.Cell>
                  </Table.Row>
                );
              })}
          </Table.Body>

          <Table.Footer>
            <Table.Row>
              <Table.HeaderCell colSpan={'10'}>
                <Select
                  placeholder=''
                  options={LOG_OPTIONS}
                  style={{ marginRight: '8px' }}
                  name='logType'
                  value={logType}
                  onChange={(e, { name, value }) => {
                    setLogType(value);
                  }}
                />
                <Button size='small' onClick={refresh} loading={loading}>
                  
                </Button>
                <Pagination
                  floated='right'
                  activePage={activePage}
                  onPageChange={onPaginationChange}
                  size='small'
                  siblingRange={1}
                  totalPages={
                    Math.ceil(logs.length / ITEMS_PER_PAGE) +
                    (logs.length % ITEMS_PER_PAGE === 0 ? 1 : 0)
                  }
                />
              </Table.HeaderCell>
            </Table.Row>
          </Table.Footer>
        </Table>
      </Segment>
    </>
  );
};

export default LogsTable;
